<template>
  <div class="container">
    <!--- <img src="../assets/grand.svg" width="50" /> -->
    <h1>ברכות יומהולדת</h1>

    <div class="create-post">
      <label for="text">כתוב ברכה:</label>
      <input type="text" id="text" v-model="text" />
      <br />
      <label for="text">שם הכותב:</label>
      <div class="w100">
        <input type="author" id="author" v-model="author" />
      </div>
      <button v-on:click="createPost" class="postit">פרסם!</button>
    </div>
    <hr />
    <p class="error" v-if="error">{{ error }}</p>
    <div class="posts-container">
      <div
        class="post"
        v-for="(post, index) in posts"
        v-bind:item="post"
        v-bind:index="index"
        v-bind:key="post._id"
      >
        <div
          class="created-at"
        >{{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}` }}</div>
        <p class="text">{{ post.text }}</p>
        <p class="author">{{post.author}}</p>
        <div class="oprations">
          <button id="delete" v-on:click="deletePost(post._id)">מחק</button>
          <button id="edit" v-on:click="popupEdit=true;">ערוך</button>
        </div>
      </div>
    </div>
    <!---***edit popup****-->
    <div id="myModal" class="edit-modal" v-if="popupEdit">
      <!-- Modal content -->
      <div class="modal-content">
        <span class="close" v-on:click="popupEdit=false;">&times;</span>
        <h3>עריכה</h3>
        <hr />
        <input type="text" id="pop-text" />
        <div class="w100">
          <input type="text" id="pop-author" />
        </div>
        <div class="pop-operations">
          <button class="save">שמור</button>
          <button class="cancle" v-on:click="popupEdit=false;">ביטול</button>
        </div>
      </div>
    </div>
    <!--**End popup**-->
  </div>
</template>

<script>
import PostService from "../PostService";

export default {
  name: "PostComponent",
  data() {
    return {
      posts: [],
      error: "",
      text: "",
      author: "",
      popupEdit: false
    };
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text, this.author);

      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      if (confirm("האם אתה בטוח?")) {
        await PostService.deletePost(id);
        this.posts = await PostService.getPosts();
      } else {
        return;
      }
    },
    async editPost(id) {
      await PostService.editPost(id, this.text, this.author);

      this.posts = await PostService.getPosts();
    }
  }
};
</script>

